import axios from 'axios';
import { api } from '../functions/api';
import { store } from '../reducers';

export const setArticles = (articles, title) => {
  return {
    type: 'SET_ARTICLES',
    code: 200,
    articles,
    title
  }
}

export const setCode = code => {
  return {
    type: 'SET_CODE',
    code
  }
}

export const setUrl = url => {
  return {
    type: 'SET_URL',
    url
  }
}

export const getArticles = (url, page = 1) => {
  store.dispatch(setCode(100));
  document.body.scrollTop = document.documentElement.scrollTop = 0;
  axios.get(api(url), { params: { page: page } })
    .then((response) => {
      if (response.data.code === 200) {
        store.dispatch(setArticles(response.data.articles, response.data.title));
        store.dispatch(setUrl(url));
      } else {
        store.dispatch(setCode(404));
      }

    })
    .catch((error) => {
      console.log(error);
    });
}



